@import "../../styles/variable.scss";
.CustomButtonStyle {
  width: 56px;
  .iconWrapper {
    width: 56px;
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    border: 1px solid $grey-color-ED;
  }
  span {
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    letter-spacing: 0.2px;
    display: block;
    color: $grey-color;
    margin-top: 16px;
  }
}
